import { mapMutations } from 'vuex';
import cloneDeep from 'lodash/cloneDeep';

function extractProps(paramKeys, sources, target = {}) {
  /**
   * 从源对象中提取指定属性到目标对象
   * @argument paramKeys 需要提取的属性名称组成的数组
   */
  for (const key of paramKeys) {
    const val = sources[key];
    if (val) {
      if (typeof val === 'object') {
        target[key] = cloneDeep(val);
      } else {
        target[key] = val;
      }
    }
  }

  return target;
}

/**
 * 组件data临时缓存mixin，用于将组件data中的数据提升到store中
 * 比如实现分页查询页面从编辑页返回后显示编辑前的页码
 * cacheKeys: 组件中待缓存的data中的属性名，属性值不能是函数
 * namespace：命名空间，处理一个路由下有多个组件的情况
 * keep：是否保留缓存结果，若为true，则不再阅读之后清除
 */
export default function(cacheKeys, namespace, keep) {
  return {
    computed: {
      moduleName() {
        return `${this.$route.path}${namespace ? '_' + namespace : ''}`;
      },
      cacheParams() {
        return this.$store.state.dataCache.params[this.moduleName];
      }
    },
    methods: {
      ...mapMutations(['SET_CACHE_PARAMS']),
      cacheParamData() {
        /**
         * 缓存组件data中的数据
         */
        this.SET_CACHE_PARAMS({
          moduleName: this.moduleName,
          params: extractProps(cacheKeys, this)
        });
      },
      recoveryDataParams() {
        /**
         * 恢复组件data中被缓存的参数,如果不传，则默认使用cacheParamKeys
         */
        if (this.cacheParams) {
          //  直接提取缓存对象中的参数到组件data中
          extractProps(cacheKeys, this.cacheParams, this);
          //  阅后即焚
          if (!keep) {
            this.SET_CACHE_PARAMS({
              moduleName: this.moduleName
            });
          }
        }
      }
    },
    created() {
      this.recoveryDataParams();
    }
  };
}
