<!-- 用户管理组件 -->
<template>
  <div class="auth-user-mamager">
    <div class="left-content">
      <div class="title">
        <div class="title-bar"></div>
        <span>部门列表</span>
      </div>
      <div class="menu-wrap">
        <el-menu
          :default-active="activeItem"
          class="company-list"
          @select="handleSelect"
        >
          <el-menu-item :index="all" @click="selectDept()">
            <div class="company-list-bar"></div>全部
          </el-menu-item>
          <el-menu-item
            v-for="item in departments"
            :key="item"
            :index="item.id"
            @click="selectDept(item)"
          >
            <div class="company-list-bar"></div>{{ `${item.deptName}` }}
          </el-menu-item>
        </el-menu>
      </div>
    </div>
    <div class="right-content">
      <div class="actions-wrap"> </div>
      <base-table-panel
        :title="title"
        class="sortTable noTableBorder"
        :data-source="userList"
        :right-btns="titleBtns()"
        :loading="userLoading"
        :columns="columns()"
        :show-summary="false"
        :total="userTotal"
        has-pagination
        :current-page="userCurrpage"
        :page-size="userPageSize"
        :border-style="true"
        :cell-class-name="roleCellClassName"
        @handleCurrent="handleClick"
        @handleSize="handleSizeChange"
        @cellClick="rowClick"
      >
        <template slot="operate-left">
          <el-form
            :inline="true"
            label-suffix=":"
            size="mini"
            class="operate-left-form"
            @submit.native.prevent
          >
            <el-form-item>
              <el-input
                v-model="userQueryStr"
                auto-complete="on"
                placeholder="请输入要搜索的内容"
                @keyup.enter.native="keySearch"
                @blur="keySearch"
              >
                <div slot="suffix" ref="inputColor1" class="backgroundChange">
                  <icon class="icon" name="icon_search" />
                </div>
              </el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="keySearch">查询</el-button>
            </el-form-item>
            <!-- <el-form-item>
              <el-select v-model="locked" filterable @change="keySearch">
                <el-option
                  v-for="item in lockedOptions"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value"
                >
                </el-option>
              </el-select>
            </el-form-item> -->
          </el-form>
        </template>
      </base-table-panel>
    </div>
    <simple-dialog
      dialog-title="复制权限"
      :dialog-visible.sync="copyVisible"
      dialog-width="600px"
      save-text="确 定"
      cancel-text="取 消"
      botton-show="true"
      @saveEmit="submitCopyForm('copyForm')"
      @cancelEmit="copyVisible = false"
    >
      <el-form ref="copyForm" :model="copyForm" :rules="copyRules">
        <el-form-item
          label="权限来自"
          prop="fromUser"
          :label-width="formLabelWidth"
        >
          <el-select
            v-model="copyForm.fromUser"
            :disabled="true"
            class="copyItem"
            placeholder="请选择"
            clearable=""
          >
            <el-option
              v-for="item in selectedFrom"
              :key="item.usercode"
              :label="item.username"
              :value="item.id"
            >
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item
          label="复制给"
          prop="toUser"
          :label-width="formLabelWidth"
        >
          <el-select
            v-model="copyForm.toUser"
            class="copyItem"
            filterable
            multiple
            collapse-tags
            placeholder="请选择"
            clearable
          >
            <el-option
              v-for="item in selectedTo"
              :key="item.usercode"
              :label="item.username"
              :value="item.id"
            >
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item
          label="复制内容"
          prop="authType"
          :label-width="formLabelWidth"
        >
          <el-checkbox-group v-model="copyForm.authType">
            <el-checkbox label="0">角色</el-checkbox>
            <el-checkbox label="1">岗位</el-checkbox>
          </el-checkbox-group>
        </el-form-item>
      </el-form>
    </simple-dialog>
    <simple-dialog
      :dialog-title="titleUser"
      :dialog-visible.sync="addUserVisible"
      dialog-width="500px"
      save-text="确 定"
      cancel-text="取 消"
      botton-show="true"
      :sureDisabled="sureDisabled"
      @saveEmit="handleAdd('addUserForm')"
      @cancelEmit="cancelAdd('addUserForm')"
    >
      <el-form
        ref="addUserForm"
        :model="addUserForm"
        label-width="80px"
        :rules="outUserFormRules"
      >
        <el-form-item label="部门 :" prop="deptId">
          <el-select
            v-model="addUserForm.deptId"
            style="width: 300px;"
            clearable
            placeholder="请选择部门"
          >
            <el-option
              v-for="item in departments"
              :key="item.id"
              :label="item.deptName"
              :value="item.id"
            >
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="姓名 :" prop="username">
          <el-input
            v-model="addUserForm.username"
            style="width: 300px;"
          ></el-input>
        </el-form-item>
        <el-form-item label="用户名 :" prop="usercode">
          <el-input
            v-model="addUserForm.usercode"
            style="width: 300px;"
            :disabled="usercodeDisabled"
          ></el-input>
        </el-form-item>
        <el-form-item label="邮箱 :" prop="email">
          <el-input
            v-model="addUserForm.email"
            style="width: 300px;"
          ></el-input>
        </el-form-item>
        <el-form-item label="手机 :" prop="mobile">
          <el-input
            v-model="addUserForm.mobile"
            style="width: 300px;"
          ></el-input>
        </el-form-item>
      </el-form>
    </simple-dialog>
  </div>
</template>

<script>
import {
  delUser,
  userDictQuery,
  userInfoLoad,
  authCopy,
  addOutUser,
  editOutUser,
  resetPassword
} from '../../../api/authority';
import paramsCache from '@mixins/paramCache';
import authorizeMixin from '@mixins/authorizeMixin';
import { mapMutations, mapState } from 'vuex';
import $utils from '@utils/util';
import mixin from '../mixin';

const paramsCacheMixin = paramsCache([
  'userCurrpage',
  'userPageSize',
  'userTotal',
  'userQueryStr',
  'locked',
  'userList',
  'title',
  'deptId',
  'deptList',
  'firstTime',
  'activeItem',
  'departments'
]);

export default {
  name: 'UserManage',
  mixins: [mixin, paramsCacheMixin, authorizeMixin],
  data() {
    const validateToUser = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请选择接受人'));
      } else if (this.copyForm.toUser.indexOf(this.copyForm.fromUser) > -1) {
        callback(new Error('接受人不能与来源人相同'));
      } else {
        callback();
      }
    };
    const checkUserName = (rule, value, callback) => {
      if (!value) {
        callback(new Error('姓名不能为空！'));
      } else {
        const reg = /^[\u4E00-\u9FA5\uf900-\ufa2d·s]{2,20}$/; // 验证姓名正则
        if (!reg.test(value)) {
          callback(new Error('请输入真实姓名！'));
        }
        callback();
      }
    };
    const checkMobile = (rule, value, callback) => {
      if (!value) {
        callback();
      } else {
        const reg = /^1[3|4|5|7|8][0-9]\d{8}$/; // 验证手机号正则
        if (!reg.test(value)) {
          callback(new Error('手机号码格式不正确！'));
        }
        callback();
      }
    };
    return {
      title: '全部',
      userQueryStr: '',
      locked: 0,
      editId: '',
      lockedOptions: [
        {
          value: 0,
          label: '未锁定'
        },
        {
          value: 1,
          label: '已锁定'
        }
      ],
      isDept: false,
      btnLoading: false,
      selectedFrom: [],
      selectedTo: [],
      copyVisible: false,
      copyRules: {
        fromUser: [
          { required: true, message: '请输入来源用户', trigger: 'change' }
        ],
        toUser: [
          { required: true, validator: validateToUser, trigger: 'change' }
        ],
        authType: [
          { required: true, message: '请选择角色类型', trigger: 'change' }
        ]
      },
      copyForm: {
        fromUser: '',
        toUser: '',
        authType: ['0']
      },
      formLabelWidth: '120px',
      addUserForm: {
        username: '',
        usercode: '',
        email: '',
        deptId: '',
        mobile: ''
      },
      outUserFormRules: {
        deptId: [
          { required: true, message: '请选择所属部门', trigger: 'change' }
        ],
        usercode: [
          { required: true, message: '请输入用户名', trigger: 'blur' }
        ],
        username: [
          { required: true, validator: checkUserName, trigger: 'blur' }
        ],
        email: [
          {
            type: 'email',
            message: '请输入正确的邮箱地址',
            trigger: ['blur', 'change']
          }
        ],
        mobile: [{ validator: checkMobile, trigger: ['blur', 'change'] }]
      },
      addUserVisible: false,
      usercodeDisabled: false, // usercode是否禁止编辑，在编辑时应为true
      deptList: [],
      masaMenu: {},
      firstTime: '1',
      activeItem: 'all',
      titleBtns() {
        const _this = this;
        return [
          {
            title: '手工同步',
            icon: 'refresh-line',
            code: 'sync',
            onClick: _this.handleLoad
          },
          {
            title: '新增用户',
            icon: 'add-line',
            code: 'add',
            onClick: _this.showAddDialog
          }
        ];
      },
      columns() {
        const _this = this;
        return [
          {
            prop: 'username',
            label: '姓名',
            'header-align': 'left',
            showOverflowTooltip: true,
            'min-width': '80'
          },
          {
            prop: 'usercode',
            label: '用户名',
            'header-align': 'left',
            showOverflowTooltip: true,
            'min-width': '80'
          },
          {
            prop: 'deptName',
            label: '部门',
            'header-align': 'left',
            showOverflowTooltip: true,
            'min-width': '100'
          },
          {
            prop: 'email',
            label: '邮箱',
            'header-align': 'left',
            showOverflowTooltip: true,
            'min-width': '140'
          },
          {
            prop: 'mobile',
            label: '手机',
            'header-align': 'left',
            showOverflowTooltip: true,
            'min-width': '140'
          },
          {
            prop: '',
            label: '操作',
            align: 'center',
            'min-width': '220',
            actions: [
              {
                code: 'edit',
                text: '编辑',
                onClick: _this.editUser
              },
              {
                code: 'grant',
                text: '授权',
                onClick: _this.roleAuth
              },
              {
                code: 'copy',
                text: '复制权限',
                onClick: _this.handleAuthCopy
              },
              {
                code: 'reset',
                text: '重置密码',
                onClick: _this.resetOutUserPassword
              },
              {
                code: 'del',
                text: '删除',
                onClick: _this.delOutUser
              }
            ]
          }
        ];
      },
      sureDisabled: false,
      titleUser: '新增用户'
    };
  },
  created() {
    !this.userList.length && this.getUsers(); //获取用户列表
    !this.departments.length && this.initDeptments();
  },
  beforeRouteLeave(to, from, next) {
    this.cacheParamData();
    next();
  },
  methods: {
    // 手工同步
    handleLoad() {
      this.$confirm('确定要同步用户信息到本系统吗?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning',
        center: true
      }).then(() => {
        this.btnLoading = true;
        userInfoLoad()
          .then(({ error }) => {
            this.btnLoading = false;
            if (!error) {
              this.$notify.success({
                title: '操作成功',
                message: '用户信息刷新成功'
              });
              this.getUsers();
            } else {
              this.$notify.error({
                title: '操作失败',
                message: error.message
              });
            }
          })
          .catch((err) => {
            this.$message.error('同步用户信息失败！');
            this.btnLoading = false;
          });
      });
    },
    // 复制权限弹窗
    handleAuthCopy(row) {
      this.copyVisible = true;
      if (this.selectedTo.length === 0) {
        userDictQuery().then(({ data }) => {
          this.selectedFrom = data;
          this.selectedTo = data;
          this.copyForm.fromUser = row.id;
        });
      } else {
        this.copyForm.fromUser = row.id;
      }
    },
    // 复制权限提交
    submitCopyForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          authCopy({
            authType: this.copyForm.authType,
            fromUser: { id: this.copyForm.fromUser },
            toUser: this.copyForm.toUser.map((item) => ({ id: item }))
          }).then(({ error }) => {
            if (!error) {
              this.$notify.success({
                title: '操作成功',
                message: '权限复制成功'
              });
              this.copyVisible = false;
            } else {
              this.$notify.error({
                title: '操作失败',
                message: error.message
              });
            }
          });
        } else {
          return false;
        }
      });
    },
    // 新增-删除
    cancelAdd(formName) {
      this.addUserVisible = false;
      this.usercodeDisabled = false;
      this.addUserForm = {
        username: '',
        usercode: '',
        email: '',
        deptId: '',
        mobile: ''
      };
      this.$refs[formName].resetFields();
    },
    // 删除外部用户
    delOutUser(user) {
      this.$confirm('确定要删除该用户?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning',
        center: true
      }).then(() => {
        delUser(user.id).then(({ error: er }) => {
          if (!er) {
            this.$message.success('用户删除成功');
            this.getUsers();
          } else {
            this.$message.error(er.message);
          }
        });
      });
    },
    handleSuccess({ status, message }) {
      if (status === 200) {
        this.$message.success('文件上传成功');
      } else {
        this.$notify.error({
          title: '操作失败',
          message: message
        });
      }
    },
    handleSelect(index) {
      this.activeItem = index;
    },
    // 添加用户
    showAddDialog() {
      this.titleUser = '新增用户';
      this.addUserVisible = true;
    },
    // 编辑用户
    editUser(row) {
      this.titleUser = '编辑用户';
      this.addUserVisible = true;
      this.editId = row.id;
      // this.addUserForm.deptId = row.deptId;
      this.addUserForm.deptId = row.deptCode;
      this.addUserForm.username = row.username;
      this.addUserForm.email = row.email;
      this.addUserForm.mobile = row.mobile;
      this.addUserForm.usercode = row.usercode;
      this.usercodeDisabled = true;
    },
    selectDept(dept) {
      if (dept) {
        this.title = dept.deptName;
        this.isDept = true;
        this.deptId = dept.id;
      } else {
        this.title = '全部';
        this.deptId = '';
      }
      this.userCurrpage = 1;
      this.getUsers();
    },
    // 关键字搜索
    keySearch() {
      this.userCurrpage = 1;
      this.getUsers();
    },
    // 进入详情
    roleAuth(row) {
      this.$router.push({
        path: '/authourityManage/authorityManage/userInfoDetail',
        query: {
          usercode: row.usercode,
          id: row.id
        }
      });
    },
    // 进入详情
    rowClick(row, column) {
      if (column.label === '姓名') {
        this.$router.push({
          path: '/authourityManage/authorityManage/userInfoDetail',
          query: {
            usercode: row.usercode,
            id: row.id
          }
        });
      }
    },
    // 添加外部用户
    handleAdd(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          const params = {
            deptId: this.addUserForm.deptId,
            username: this.addUserForm.username,
            usercode: this.addUserForm.usercode,
            email: this.addUserForm.email,
            mobile: this.addUserForm.mobile
          };
          this.sureDisabled = true;
          // 判断是添加还是编辑用户
          if (this.titleUser === '新增用户') {
            addOutUser(params).then(({ error }) => {
              if (error) {
                this.$message.warning(error.message);
                this.sureDisabled = false;
                return;
              } else {
                this.$message.success({
                  title: '成功',
                  message: '添加用户成功'
                });
              }
              this.sureDisabled = false;
              this.addUserVisible = false;
              this.$refs[formName].resetFields();
              this.getUsers();
            });
          } else {
            params.id = this.editId;
            editOutUser(params).then(({ error }) => {
              if (error) {
                this.$message.warning(error.message);
                this.sureDisabled = false;
                return;
              } else {
                this.$message.success({
                  title: '成功',
                  message: '编辑用户成功'
                });
              }
              this.sureDisabled = false;
              this.addUserVisible = false;
              this.usercodeDisabled = false;
              this.$refs[formName].resetFields();
              this.getUsers();
            });
          }
        } else {
          return false;
        }
      });
    },
    // 重置用户密码
    resetOutUserPassword(row) {
      this.$confirm(`确定要重置${row.username}的登陆密码?`, '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning',
        center: true
      }).then(() => {
        resetPassword(row.id).then(({ error: er }) => {
          if (!er) {
            this.$message.success('用户密码重置成功');
          } else {
            this.$message.error(er.message);
          }
        });
      });
    },
    ...mapMutations(['changeSelectedUser'])
  }
};
</script>

<style lang="scss" scoped>
.auth-user-mamager {
  position: relative;
  min-height: calc(100vh - 130px);
  box-sizing: border-box;
  background-color: #fdfdfd;
  overflow: hidden;
  border-radius: 4px;
  display: flex;
  padding: 0 20px;

  .left-content,
  .right-content {
    .title {
      height: 50px;
      line-height: 50px;
      border-bottom: 1px solid $color-table-cutting;
      margin-bottom: 20px;
      .title-bar {
        display: inline-block;
        width: 5px;
        height: 16px;
        background-color: #2a76cd;
        position: relative;
        top: 2px;
        margin-right: 6px;
      }
      span {
        color: $color-subtitle-text;
        font-size: 16px;
      }
    }
  }

  .left-content {
    min-width: 220px;
    width: 220px;
    border-right: 1px solid $color-table-cutting;
    padding-right: 20px;
    margin: 20px 0;
    .title {
      position: relative;
      top: -20px;
    }
    .menu-wrap {
      position: relative;
      top: -20px;
      height: calc(100vh - 242px);
      overflow-y: scroll;
      &::-webkit-scrollbar {
        width: 6px;
        height: 0;
      }
      &::-webkit-scrollbar-thumb {
        border-radius: 4px;
        background: #dedfe0;
      }
      .company-list {
        border-right: none;
        .company-list-bar {
          width: 4px;
          height: 40px;
          position: absolute;
          left: 0;
          background-color: #2a76cd;
          display: none;
        }
      }
    }
  }

  .right-content {
    width: 100%;
    min-width: 840px;
    box-sizing: border-box;
    padding-left: 20px;

    .common-table {
      .tag3 {
        background-color: rgba(43, 188, 184, 0.15);
        color: #2bbcb8;
        width: max-content;
        border-radius: 15px;
        padding: 0 10px;
      }
    }

    .actions-wrap {
      overflow: hidden;
      margin-bottom: 12px;

      .btn-wrap {
        float: right;
        .handel-btn {
          color: #4b556a;
          height: 32px;
          padding: 0 15px;
          .icon {
            width: 14px;
            height: 14px;
            margin-right: 4px;
            color: #b5b5b5;
          }
          &:hover {
            .icon {
              color: #2a76cd;
            }
          }
        }
      }
    }

    .text-btn {
      color: #2a76cd;
      font-size: 14px;
      cursor: pointer;
      margin-left: 20px;
      &:first-child {
        margin-left: 0;
      }
      i {
        width: 12px;
        height: 12px;
      }
    }
    .pagination {
      margin-top: 20px;
    }
  }
  .copyItem {
    width: 400px;
  }
}
</style>
<style lang="scss">
.company-list {
  .el-menu-item.is-active {
    color: $color-main;
    background-color: $color-main-opcity;
  }
  .el-menu-item:hover {
    background-color: $color-main-opcity;
    color: $color-main;
  }
}
</style>
