import { mapState } from 'vuex';
import { arrayFindDeep, arrayFind } from '../utils/commonUtil';

export default {
  computed: {
    ...mapState({
      authorizes: (state) => state.user.authorizes,
      authSwitch: (state) => state.user.authSwitch
    })
  },
  methods: {
    authentication(code) {
      let authorized = true;
      if (this.authSwitch) {
        authorized = false;
        const currentMenu = this.getCurrentMenu();

        const currMenu = arrayFindDeep(
          this.authorizes,
          'authorize',
          currentMenu
        );
        if (currMenu && currMenu.actions) {
          const currCode = `${currentMenu}:${code}`;
          authorized = currMenu.actions.some((item) => item.code === currCode);
        }
      }
      return authorized;
    },
    getCurrentMenu() {
      const r = this.$route;
      let currCode = r.meta.authority;

      const index = r.path.lastIndexOf('/');
      if (index > 0) {
        const parentRouter = arrayFind(
          r.matched,
          'path',
          r.path.slice(0, index)
        );
        if (parentRouter) {
          if (parentRouter.meta) {
            currCode = `${parentRouter.meta.authority}_${currCode}`;
          } else {
            currCode = `${parentRouter.path}_${currCode}`;
          }
        }
      }

      return currCode;
    }
  }
};
